@import url(https://fonts.googleapis.com/css?family=Roboto);
.dialogError {
    border-top: 10px solid #d32f2f;
}

.dialogSuccess {
    border-top: 10px solid #689f38;
}

input[type=text].valid {
    border: 2px solid #B2FF59;
}

input[type=text].no-valid {
    border: 2px solid #FF6E40;
}

.listContent {
    padding: 25px 0;
}

.formElement {
    margin: 25px auto;
}

.selectElement {
    margin-bottom: 75px;
}

button.formElement {
    margin-bottom: 15px;
}

.addressBookCard {
    margin-bottom: 20px;
}

.addressFormContainer {
    border-top: 15px solid #212121;
    border-radius: 1%;
}

.addressListContainer {
    border-top: 15px solid #5D4037;
    border-radius: 1%;
}

.formContent h2.cardTitleHeader {
    color: #212121;
    border-bottom: 5px solid #21212140;
    margin-bottom: 20px;
    padding-bottom: 15px;
}

button.personalizedButton {
    font-weight: bold;
}

.addressTable>thead tr th {
    font-size: 1.25em;
}

.addressTable>tbody tr th p,
.addressTable>tbody tr td p{
    font-size: 1.15em;
}

body {
    background-color: #FFEB3B;
    padding: 20px 50px;
    margin: 0;
    font-family: 'Roboto', sans-serif;
}
.top-static-ui {
    text-align: center;
}
ul.header li {
    display: inline;
    list-style-type: none;
    margin: 0;
}
ul.header {
    background-color: #212121;
    padding: 0;
}
ul.header li a {
    color: #FFF;
    font-weight: bold;
    font-size: 20px;
    text-decoration: none;
    padding: 20px;
    display: inline-block;
}
.content {
    padding: auto 50px;
    text-align: left;
}
.content h2 {
    padding: 0;
    margin: 0;
}
.content li {
    margin-bottom: 10px;
}

.img-logo {
    width: 10%;
    margin: 25px;
}

